import React, {PureComponent} from 'react'
import {
  message,
  Button
} from 'antd'
import {
  API_STORAGELOCATIONS_IMPORT,
  API_STORAGELOCATIONS_IMPORTSTORAGESORT
} from 'api'
import {Import} from 'HOC'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {setVariant} from 'Variant'

@connect(
  state => ({...state.storageLocation, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  /* 下载模版 */
  handleDownLoad = (key) => {
    const downloadData = JSON.parse(localStorage.getItem('b2bWmsDownload'))
    console.log(downloadData)
    window.open(downloadData[key].downloadUrl)
  }

  /* 导入成功 */
  handleImportSuccess = res => {
    const {handleGetList, searchParams, page, size} = this.props
    res && res.message === 'ok' && message.success('导入成功')
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  /* 报错导出 */
  handleExprotError = () => {
  }
  // 导出
  handleExport = () => {
    const {handleExport, searchParams, page, size} = this.props
    handleExport({searchParams: {page, size, ...searchParams}}, () => {
      message.success('导出成功')
    })
  }
    // 导出PDF
  handleExportPDF = () => {
    const {handleExportPDF, searchParams, page, size} = this.props
    handleExportPDF({searchParams: {page, size, ...searchParams}}, () => {
      message.success('导出成功')
    })
  }
  /* 打印预览 */
  handleBrowse = (record) => {
    const {selectedRowKeys} = this.props
    if (!selectedRowKeys.length) {
      message.error('请选择需要打印的储位')
      return
    }
    setVariant('wms/storageLocation', selectedRowKeys)
    window.open('/wms/storageLocation/Print', 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
  }
  render() {
    const {userLoation} = this.props
    return (
      <div className={style['table-tool']}>
        {
          userLoation && userLoation.type == 1 && <Button type="primary" className={style.btn} onClick={() => {
            this.handleDownLoad('T02')
          }}>模板下载</Button>
        }
        {
          userLoation && userLoation.type == 1 && <Import
          action={{api: API_STORAGELOCATIONS_IMPORT}}
          onOk={this.handleImportSuccess}
          onExprotError={this.handleExprotError}
          beforeImport={() => {
            const {update} = this.props
            update({tableLoading: true})
          }}
          onResponse={() => {
            const {update} = this.props
            update({tableLoading: false})
          }}
        >
          <Button type="primary" className={style.btn}>批量导入</Button>
        </Import>
        }
        <Button type="primary" className={style.btn} onClick={this.handleExport}>导出</Button>
        <Button type="primary" className={style.btn} onClick={this.handleExportPDF}>导出PDF</Button>
        <Button type="primary" className={style.btn} onClick={this.handleBrowse}>打印储位条码</Button>
        <Button type="primary" className={style.btn} onClick={() => {
          this.handleDownLoad('T15')
        }}>修改储位顺序模板下载</Button>
        <Import
          action={{api: API_STORAGELOCATIONS_IMPORTSTORAGESORT}}
          onOk={this.handleImportSuccess}
          onExprotError={this.handleExprotError}
          beforeImport={() => {
            const {update} = this.props
            update({tableLoading: true})
          }}
          onResponse={() => {
            const {update} = this.props
            update({tableLoading: false})
          }}
        >
          <Button type="primary" className={style.btn}>导入修改储位顺序</Button>
        </Import>
      </div>
    )
  }
}



