import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Checkbox,
  Input
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect} from 'Form'
import {API_STORAGELOCATIONS_RESERVOIRAREAS, API_DICTIONARYS} from 'api'

@connect(
  state => ({...state.storageLocation, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 新增
  handleAdd = () => {
    const {update} = this.props
    update({isShowModal: true, modalTitle: '新增储位', modalData: ''})
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
  }
  render() {
    const {
      searchParams: {
        reservoirAreaCode,
        storageLocationTypeCode,
        storageLocationStatus,
        mixingGoodsFlag,
        storageLocationCode
        // mixingShipperFlag
      },
      userLoation
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <CsxSelect
              dateApi={API_STORAGELOCATIONS_RESERVOIRAREAS}
              label='库区编号'
              selectItem={{data: reservoirAreaCode, key: 'key', valueKey: 'key'}}
              onChangeSelect={(value) => {
                this.onInputChange({reservoirAreaCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              label='储位类型'
              dateApi={API_DICTIONARYS}
              searchParams={{dicTypes: 'STORAGEYPE'}}
              selectItem={{data: storageLocationTypeCode, key: 'dicKey', valueKey: 'dicValue'}}
              onChangeSelect={(value) => {
                this.onInputChange({storageLocationTypeCode: value})
              }}
            />
          </Col>
          <Col span={7}>
            <div className='commonInput'>
              <label>储位编码/名称:</label>
              <div className='box'>
                <Input value={storageLocationCode} onChange={(e) => {
                  this.onInputChange({storageLocationCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={12}>
            <Checkbox checked={storageLocationStatus ? true : false} onChange={(e) => {
              this.onInputChange({storageLocationStatus: e.target.checked})
            }}>商品绑定</Checkbox>
            <Checkbox checked={mixingGoodsFlag ? true : false} onChange={(e) => {
              this.onInputChange({mixingGoodsFlag: e.target.checked})
            }}>支持商品混放</Checkbox>
            {/* <Checkbox checked={mixingShipperFlag ? true : false} onChange={(e) => {
              this.onInputChange({mixingShipperFlag: e.target.checked})
            }}>支持货主混放</Checkbox> */}
          </Col>
          <Col span={12}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
              {
                userLoation && userLoation.type == 1 && <Button type="primary" onClick={this.handleAdd}>新增</Button>
              }
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



