import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Input,
  Form,
  Button,
  Checkbox,
  message
} from 'antd'
import {CsxFormSelect} from 'Form'
import {API_STORAGELOCATIONS_RESERVOIRAREAS, API_DICTIONARYS, API_STORAGELOCATIONS_WAREHOUSETYPES} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const FormItem = Form.Item

@connect(
  state => ({...state.storageLocation, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    // 数据初始化
    const {modalData, update, userLoation} = this.props
    let isAllowGoods = false
    // 判断是新增还是编辑
    if (modalData) {
      let reqObj = {...modalData}
      // 特殊处理数据
      reqObj.reservoirAreaCode = {
        value: reqObj.reservoirAreaCode && reqObj.reservoirAreaCode.toString(),
        children: reqObj.reservoirAreaName
      }
      reqObj.storageLocationCategoryCode = {
        value: reqObj.storageLocationCategoryCode && reqObj.storageLocationCategoryCode.toString(),
        children: reqObj.storageLocationCategoryName && reqObj.storageLocationCategoryName.toString()
      }
      reqObj.storageLocationTypeCode = {
        value: reqObj.storageLocationTypeCode && reqObj.storageLocationTypeCode.toString(),
        children: reqObj.storageLocationTypeName && reqObj.storageLocationTypeName.toString()
      }
      reqObj.warehouseCode += reqObj.warehouseName
      if (reqObj.storageLocationTypeCode && reqObj.storageLocationTypeCode.children && reqObj.storageLocationTypeCode.children == '存储位') {
        isAllowGoods = true
      }
      update({modalData: {...reqObj}, isAllowGoods})
    } else {
      let mixingShipperFlag = false,
        mixingGoodsFlag = false,
        warehouseCode = userLoation.value + userLoation.children
      update({modalData: {mixingShipperFlag, mixingGoodsFlag, warehouseCode}, isAllowGoods})
    }
  }
  handleSelectChange = () => {
    const {form} = this.props
    let inputObj = form.getFieldsValue()
    // 储位编码
    let storageLocationCode = `${inputObj.reservoirAreaCode && inputObj.reservoirAreaCode.value && inputObj.reservoirAreaCode.value.trim() || ''}${inputObj.storageLocationAisleon && inputObj.storageLocationAisleon.trim() || ''}${inputObj.storageRow && inputObj.storageRow.trim() || ''}${inputObj.storageColumn && inputObj.storageColumn.trim() || ''}${inputObj.storageLayer && inputObj.storageLayer.trim() || ''}`
    // 容积
    let storageLocationVolume = ((inputObj.storageLocationLength || 1) * (inputObj.storageLocationWidth || 1) * (inputObj.storageLocationHeight || 1)).toFixed(2)
    form.setFieldsValue({
      storageLocationCode,
      storageLocationVolume
    })
  }
  handleSubmit = (e) => {
    const {form, modalData, handleAddOrEdit, userLoation} = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = {...values}
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            if (i == 'reservoirAreaCode') {
              reqObj.reservoirAreaName = reqObj[i].children
            }
            if (i == 'storageLocationCategoryCode') {
              reqObj.storageLocationCategoryName = reqObj[i].children
            }
            if (i == 'storageLocationTypeCode') {
              reqObj.storageLocationTypeName = reqObj[i].children
            }
            reqObj[i] = reqObj[i].value
          }
        }
        reqObj.warehouseCode = userLoation.value
        reqObj.warehouseName = userLoation.children
        let result = {
            data: reqObj
          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (modalData && modalData.id) {
          result.data.id = modalData.id
          result.urlParams = [modalData.id]
          msg = '编辑成功'
        }
        handleAddOrEdit({...result}, () => {
          message.success(msg)
          const {handleGetList, searchParams, page, size} = this.props
          handleGetList({searchParams: {page, size, ...searchParams}})
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false})
  }
  render() {
    const {modalTitle, form: {getFieldDecorator}, modalTuttonLoading, modalData, isAllowGoods} = this.props
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 8}
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 16}
      }
    }
    console.log(modalData, modalData.id)
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
        width='700px'
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <Row gutter={16}>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="所属仓库"
              >
                {getFieldDecorator('warehouseCode', {
                  initialValue: modalData.warehouseCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <Input disabled />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="库区"
                hasFeedback
              >
                {getFieldDecorator('reservoirAreaCode', {
                  initialValue: modalData.reservoirAreaCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    antd={{disabled: (modalData && modalData.id) ? true : false}}
                    dateApi={API_STORAGELOCATIONS_RESERVOIRAREAS}
                    selectItem={{key: 'key', valueKey: ['key', 'value']}}
                    handleSelectChange={this.handleSelectChange}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="通道"
                hasFeedback
              >
                {getFieldDecorator('storageLocationAisleon', {
                  initialValue: modalData.storageLocationAisleon
                })(
                  <Input maxLength={4} disabled = {(modalData && modalData.id) ? true : false} onBlur={this.handleSelectChange}/>
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <FormItem
                labelCol = {{
                  xs: {span: 24},
                  sm: {span: 16}
                }}
                wrapperCol = {{
                  xs: {span: 24},
                  sm: {span: 8}
                }}
                style={{
                  width: '50%',
                  display: 'inline-block'
                }}
                label="排-列-层"
              >
                {getFieldDecorator('storageRow', {
                  initialValue: modalData.storageRow,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <Input maxLength={4} disabled = {(modalData && modalData.id) ? true : false} onBlur={this.handleSelectChange}/>
                )}
              </FormItem>
              <FormItem
                wrapperCol = {{
                  xs: {span: 24},
                  sm: {span: 16, push: 8}
                }}
                style={{
                  width: '25%',
                  display: 'inline-block'
                }}
              >
                {getFieldDecorator('storageColumn', {
                  initialValue: modalData.storageColumn,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <Input maxLength={4} disabled = {(modalData && modalData.id) ? true : false} onBlur={this.handleSelectChange}/>
                )}
              </FormItem>
              <FormItem
                wrapperCol = {{
                  xs: {span: 24},
                  sm: {span: 16, push: 8}
                }}
                style={{
                  width: '25%',
                  display: 'inline-block'
                }}
              >
                {getFieldDecorator('storageLayer', {
                  initialValue: modalData.storageLayer,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <Input maxLength={4} disabled = {(modalData && modalData.id) ? true : false} onBlur={this.handleSelectChange}/>
                )}
              </FormItem>
            </Col>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="储位编码"
                hasFeedback
              >
                {getFieldDecorator('storageLocationCode', {
                  initialValue: modalData.storageLocationCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <Input disabled = {(modalData && modalData.id) ? true : false} placeholder='库区+通道+排+列+层' />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <FormItem
                // labelCol = {{
                //   xs: {span: 24},
                //   sm: {span: 4}
                // }}
                // wrapperCol = {{
                //   xs: {span: 24},
                //   sm: {span: 20}
                // }}
                {...formItemLayout}
                label="储位名称"
                hasFeedback
              >
                {getFieldDecorator('storageLocationName', {
                  initialValue: modalData.storageLocationName
                })(
                  <Input />
                )}
              </FormItem>
            </Col>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="储位类别"
                hasFeedback
              >
                {getFieldDecorator('storageLocationCategoryCode', {
                  initialValue: modalData.storageLocationCategoryCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_DICTIONARYS}
                    searchParams={{dicTypes: 'STORAGECLASS'}}
                    selectItem={{key: 'dicKey', valueKey: 'dicValue'}}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="储位类型"
                hasFeedback
              >
                {getFieldDecorator('storageLocationTypeCode', {
                  initialValue: modalData.storageLocationTypeCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_STORAGELOCATIONS_WAREHOUSETYPES}
                    searchParams={{type: 'T', operationType: 'I'}}
                    selectItem={{key: 'key', valueKey: 'value'}}
                    handleSelectChange={(value) => {
                      const {update, isAllowGoods} = this.props
                      update({isAllowGoods: value.children == '存储位' ? true : false})
                    }}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <FormItem
                labelCol = {{
                  xs: {span: 24},
                  sm: {span: 16}
                }}
                wrapperCol = {{
                  xs: {span: 24},
                  sm: {span: 8}
                }}
                style={{
                  width: '50%',
                  display: 'inline-block'
                }}
                label="长-宽-高CM"
              >
                {getFieldDecorator('storageLocationLength', {
                  initialValue: modalData.storageLocationLength,
                  rules: [{
                    required: true, message: '请填写数字!', pattern: /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/
                  }]
                })(
                  <Input onBlur={this.handleSelectChange}/>
                )}
              </FormItem>
              <FormItem
                wrapperCol = {{
                  xs: {span: 24},
                  sm: {span: 16, push: 8}
                }}
                style={{
                  width: '25%',
                  display: 'inline-block'
                }}
              >
                {getFieldDecorator('storageLocationWidth', {
                  initialValue: modalData.storageLocationWidth,
                  rules: [{
                    required: true, message: '请填写数字!', pattern: /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/
                  }]
                })(
                  <Input onBlur={this.handleSelectChange}/>
                )}
              </FormItem>
              <FormItem
                wrapperCol = {{
                  xs: {span: 24},
                  sm: {span: 16, push: 8}
                }}
                style={{
                  width: '25%',
                  display: 'inline-block'
                }}
              >
                {getFieldDecorator('storageLocationHeight', {
                  initialValue: modalData.storageLocationHeight,
                  rules: [{
                    required: true, message: '请填写数字!', pattern: /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/
                  }]
                })(
                  <Input onBlur={this.handleSelectChange}/>
                )}
              </FormItem>
            </Col>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="容积M3"
              >
                {getFieldDecorator('storageLocationVolume', {
                  initialValue: modalData.storageLocationVolume,
                  rules: [{
                    pattern: /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/, message: '请填写数字!'
                  }]
                })(
                  <Input disabled />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="备注"
                hasFeedback
              >
                {getFieldDecorator('remark', {
                  initialValue: modalData.remark
                })(
                  <Input />
                )}
              </FormItem>
            </Col>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="排序"
                hasFeedback
              >
                {getFieldDecorator('sortNumber', {
                  initialValue: modalData.sortNumber,
                  rules: [{
                    required: true, message: '请填写正整数!', pattern: /^\d+$/
                  }]
                })(
                  <Input />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={6}>
              <FormItem
                labelCol = {{
                  xs: {span: 24},
                  sm: {span: 16}
                }}
                wrapperCol = {{
                  xs: {span: 24},
                  sm: {span: 8}
                }}
                label="允许混放商品"
              >
                {getFieldDecorator('mixingGoodsFlag', {
                  initialValue: modalData.mixingGoodsFlag,
                  valuePropName: 'checked'
                })(
                  // <Checkbox disabled={isAllowGoods}/>
                  <Checkbox />
                )}
              </FormItem>
            </Col>
            {/* <Col span={6}>
              <FormItem
                labelCol = {{
                  xs: {span: 24},
                  sm: {span: 16}
                }}
                wrapperCol = {{
                  xs: {span: 24},
                  sm: {span: 8}
                }}
                label="允许混合货主"
              >
                {getFieldDecorator('mixingShipperFlag', {
                  initialValue: modalData.mixingShipperFlag,
                  valuePropName: 'checked'
                })(
                  <Checkbox />
                )}
              </FormItem>
            </Col> */}
          </Row>
          <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{marginRight: '48px'}} onClick={this.handleCloseModal}>取消</Button>
              <Button type="primary" htmlType="submit" loading={modalTuttonLoading}>保存</Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



